export default function () {
  const options = {
    identifier: webviewIdentifier,
    width: 800,
    height: 610,
    show: false,
    resizable: false,
    movable: true,
    fullscreen: false,
  };

  const browserWindow = new BrowserWindow(options);
  // Stores base Colors
  let baseColors = [];
  let isArsenic = true;

  const webContents = browserWindow.webContents;
  const allLayerStyles = document.sharedLayerStyles;

  if (sketchAssets.isArsenicDesignSystem(allLayerStyles) == false) {
    isArsenic = false;
  }

  // only show the window when the page has loaded to avoid a white flash
  browserWindow.once("ready-to-show", () => {
    browserWindow.show();
  });

  // print a message when the page loads
  webContents.on("did-finish-load", () => {
    try {
      // Check if this is Arsenic design system or not.
      if (sketchAssets.isArsenicDesignSystem(allLayerStyles) == false) {
        UI.message(" This is NOT an Arsenic design system.");
      } else {
        //UI.message(" Arsenic Library plugin loaded successfully!");
        const splitDocPath = document.path.split("/");
        const documentName = splitDocPath[splitDocPath.length - 1];
        webContents
          .executeJavaScript(
            `setOpenDocumentName(${JSON.stringify(documentName)})`
          )
          .catch(console);
        getBase500Colors(allLayerStyles);
        getFontsList();
      }
    } catch (err) {
      console.log(err, " error in did-finish-load");
    }
  });

  // add a handler for a call from web content's javascript
  webContents.on(
    "doUpdateColors",
    (inputId, hexColor, minLuminence, maxLuminence) => {
      try {
        const swatchName = sketchAssets.capitalizeString(inputId);
        //const sketchVersion = MSApplicationMetadata.metadata().appVersion;

        //if (sketchVersion >= 69) {
        const swatchContainer = document.sketchObject
          .documentData()
          .sharedSwatches();
        const mySwatchs = swatchContainer.swatches();
        const allLayerStyles = document.sharedLayerStyles;

        let isColorExist = baseColors.find((data) => {
          if (data.name == inputId && data.value == hexColor + "ff")
            return true;
          else return false;
        });
        // if (!isColorExist) {
        // mySwatchs.forEach(function (swatch) {
        for (let i = 0; i < mySwatchs.length; i++) {
          const swatch = mySwatchs[i];

          if (sketchAssets.capitalizeString(swatch.name()) == swatchName) {
            let newHexColor = MSColor.colorWithHex_alpha(hexColor, 1.0);
            swatch.updateWithColor(newHexColor);
            swatchContainer.updateReferencesToSwatch(swatch);
            sketchAssets.changeColorShadeAssets(
              allLayerStyles,
              hexColor,
              swatchName,
              minLuminence,
              maxLuminence
            );
            const regex = ".*" + swatchName + "-Outline-.*px 50$";
            sketchAssets.updatePrimaryBorders(
              allLayerStyles,
              newHexColor,
              regex
            );
          }
        }
        //}
        //}
      } catch (err) {
        UI.message(" Unable to update colors!");
        console.log(err, " error in doUpdateColors");
      }
    }
  );

  // add a handler for a call from web content's javascript
  webContents.on("doUpdateFonts", (fontTypeLayers, fontName) => {
    try {
      const newFontTypeLayers = fontTypeLayers.replace("-", " ");
      updateFontStyle(fontName, newFontTypeLayers);
    } catch (err) {
      UI.message(" Unable to update font styles!");
      console.log(err, " error in doUpdateColors");
    }
  });

  // add a handler for a call from web content's javascript
  webContents.on("setMessage", (whichElement) => {
    try {
      if (whichElement === "colors")
        UI.message(" Colors has been updated successfully!");
      else UI.message(" Font style has been updated successfully!");
    } catch (err) {
      console.log(err, " error in doUpdateColors");
    }
  });

  webContents.on("doResetColor", (inputId) => {
    const initialColors = getBase500Colors(allLayerStyles, true);
    let isColorExist = initialColors.find((data) => {
      if (data.name == inputId) return true;
      else return false;
    });
    console.log(isColorExist, "isColorExist in doResetColor");
    webContents
      .executeJavaScript(
        `setInitialColors(${JSON.stringify(isColorExist)}, true)`
      )
      .catch(console);
  });
  // add a handler for a call from web content's javascript
  webContents.on("closeWebView", () => {
    try {
      onShutdown();
    } catch (err) {
      console.log(err, " error in closeWebView");
    }
  });

  webContents.on("openExternalLink", (url) => {
    try {
      console.log(url, "entered in openExternalLink");
      NSWorkspace.sharedWorkspace().openURL(NSURL.URLWithString(url));
      onShutdown();
    } catch (err) {
      console.log(err, " error in openExternalLink");
    }
  });

  webContents.on("getFonts", () => {
    try {
      getFontsList();
    } catch (err) {
      console.log(err, " error in getFonts");
    }
  });

  const getFontsList = () => {
    const availableFontFamilies =
      NSFontManager.sharedFontManager().availableFontFamilies();
    const arrayFontList = Array.from(availableFontFamilies);
    const objFontList = Object.entries(arrayFontList);

    for (let i = 0; i < objFontList.length; i++) {
      webContents
        .executeJavaScript(
          `setInitialFonts(${JSON.stringify(String(objFontList[i][1]))})`
        )
        .catch(console);
    }
  };

  const updateFontStyle = (fontName, fontTypeLayers) => {
    // Filter the array based on value
    const textStyles = document.sharedTextStyles;
    let filteredTextStylesToUpdate = textStyles.filter((layer) => {
      // "Primary/.*\Outline-1px$"
      // ".*\Outline-.*\px$"
      return layer.name.match(fontTypeLayers + "/.*");
    });

    // Now as per the selection of the user from the plugin, update the text styles.
    filteredTextStylesToUpdate.length > 0 &&
      filteredTextStylesToUpdate.forEach(function (sharedStyle, i) {
        if (sharedStyle.type == "SharedStyle") {
          // Get all the instance of the layer which are using the same Shared Layer Styles.
          // Update the Shared Text layer Styles
          sharedStyle.style.fontFamily = fontName;

          const layers = sharedStyle.getAllInstancesLayers();

          // Loop through all the layers and update the layer style.
          layers.forEach(function (fontLayers, i) {
            if (fontLayers.type == "Text") {
              fontLayers.style.syncWithSharedStyle(sharedStyle);
            }
          });
        } else {
          console.log(" Not a Shared Style");
        }
      });
    return true;
  };

  const getBase500Colors = (layerStyles, isReset) => {
    try {
      // Filter the array based on value
      let filteredBaseLayers = layerStyles.filter((layer) =>
        // "Primary/.*\Outline-1px$"
        // ".*\Outline-.*\px$"
        layer.name.match(".*Base-500$")
      );

      // get IDs of shared layer styles
      filteredBaseLayers.map(function (sharedLayerStyle, key) {
        // filteredBaseLayers.forEach((sharedLayerStyle) => {
        if (sharedLayerStyle.style.fills.length > 0) {
          const colorName = sharedLayerStyle.name.split(/[/0-9 \?&]/g);
          let colorCodeObj = {
            name: colorName[3].toLowerCase(),
            value: sharedLayerStyle.style.fills[0].color,
            isPrimary: key === 0 ? true : false,
            isColorChanged: false,
          };
          baseColors.push(colorCodeObj);
          if (!isReset) {
            webContents
              .executeJavaScript(
                `setInitialColors(${JSON.stringify(colorCodeObj)})`
              )
              .catch(console);
          }
        }
      });
      return baseColors;
    } catch (err) {
      console.log(err, "error in getBase500Colors");
    }
  };

  if (isArsenic) {
    browserWindow.loadURL(require("../resources/plugin-options.html"));
  } else {
    browserWindow.loadURL(require("../resources/index.html"));
  }
}

// When the plugin is shutdown by Sketch (for example when the user disable the plugin)
// we need to close the webview if it's open
export function onShutdown() {
  const existingWebview = getWebview(webviewIdentifier);
  if (existingWebview) {
    existingWebview.close();
  }
}
